.form {
  padding: 44px 32px 32px;
  border-radius: 12px;
  background-color: $white;

  @include max("sm") {
    padding: 30px 15px;
  }

  &__title {
    margin-bottom: 36px;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: -0.015em;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  &__input {
    margin-bottom: 20px;
    width: calc(50% - 15px);

    @include max("sm") {
      width: calc(50% - 10px);
    }

    @include max("xs") {
      width: 100%;
    }

    &:nth-child(2n) {
      margin-left: 30px;

      @include max("sm") {
        margin-left: 20px;
      }

      @include max("xs") {
        margin-left: 0;
      }
    }
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    background: $gradient;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__extra {
    align-items: center;
    margin-bottom: 12px;

    @include max("sm") {
      flex-direction: column-reverse;
    }

    &-file {
      margin-left: auto;
      margin-bottom: 20px;
    }

    &-text {
      @include max("sm") {
        width: 100%;
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include max("md") {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__button {
    min-width: 227px;

    @include max("md") {
      align-self: flex-end;
      margin-top: 20px;
    }

    @include max("sm") {
      align-self: center;
    }
  }
}
